<div class="log"><div class="search"><a-form class="form" layout="inline" :model="searchForm"><a-form-item label="登录人员:" name="name"><a-select style="width: 160px" v-model:value="searchForm.code" placeholder="登录人员" allowClear show-search @search="getUserLists" :filterOption="false"><a-select-option v-for="item in userList" :value="item.code" :key="item.code">{{ item.name }}</a-select-option></a-select></a-form-item><a-form-item label="时间:"><a-range-picker v-model:value="searchForm.time" showTime></a-range-picker></a-form-item><a-form-item class="btns"><a-button type="primary" @click="submitSearch(1)">查询</a-button><a-button @click="resetSearch">重置</a-button></a-form-item></a-form></div><div class="list1"><a-table :columns="columns" :data-source="data" :pagination="pagination" :loading="queryLogLoding" rowKey="id" @change="pageChange"><template v-slot:logincode="{ text, record }"><a-button type="link" @click="goToInfo(record)">{{ text }}</a-button></template></a-table></div></div>